























import uniIcon from '../uni-icon/uni-icon.vue'
import uniBadge from '../uni-badge/uni-badge.vue'
export default {
	name: 'uni-list-item',
	components: {
		uniIcon,
		uniBadge
	},
	data() {
		return {

		};
	},
	props: {
		title: String, //
		note: String, //
		disabled: { //
			type: [Boolean, String],
			default: false
		},
		showArrow: { //
			type: [Boolean, String],
			default: true
		},
		showBadge: { //
			type: [Boolean, String],
			default: false
		},
		showSwitch: { //Switch
			type: [Boolean, String],
			default: false
		},
		switchChecked: { //Switch
			type: [Boolean, String],
			default: false
		},
		badgeText: String, //badge
		badgeType: { //badge
			type: String,
			default: 'success'
		},
		thumb: String, //
		showExtraIcon: { //
			type: [Boolean, String],
			default: false
		},
		extraIcon: {
			type: Object,
			default () {
				return {
					type: "contact",
					color: "#000000",
					size: "20"
				};
			}
		}
	},
	methods: {
		onClick() {
			this.$emit('click')
		},
		onSwitchChange(e) {
			this.$emit('switchChange', e.detail)
		}
	}
}
